.phone {
  display: flex;
  flex-wrap: wrap;

  align-items: center;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  color: $default-color;
  text-decoration: none;

  &--black {
    color: $text-color;
  }

  & > span {
    // font-size: 1.5625rem;
    @include ff("font-size", $phoneTitle);
  }

  & > span > span {
    font-size: 0.9375rem;
    font-family: $mainFont;
    font-weight: $mainFontWeight;
  }

  & span {
    display: block;

    @include for-size(tablet-portrait-down) {
      display: none;
    }
  }
  & svg {
    padding-right: 0.9375rem;
    box-sizing: content-box;

    @include for-size(tablet-portrait-down) {
      padding-right: 0;
    }
  }
}
