.pagination {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: calc(var(--padding-and-margin) * 3);

  &__item {
    margin: 0 5px;
    padding: 5px;
    color: $primary-color;
    text-decoration: none;

    &:focus,
    &:hover {
      color: $text-color;
      text-decoration: none;
    }

    &:active,
    &.active {
      color: $text-color;
    }
  }
}
