*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: $mainFont;
  font-size: $mainFontSize;
  font-weight: $mainFontWeight;
  line-height: $mainLineHeight;
  color: $text-color;

  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

h2:not([class]),
h3:not([class]),
h4:not([class]),
h5:not([class]),
h6:not([class]) {
  margin-bottom: $padding-and-margin;
  font-size: 1rem;
}

ul[class] {
  padding: 0;
  margin: 0;
  list-style: none;
  list-style-type: none;
}

// footer к низу

.wrapper {
  display: flex;
  flex-direction: column;

  width: 100%;
  min-height: 100%;
}

.overlay {
  overflow: hidden;
  height: 100vh;
}

.scroll-content {
  overflow-y: auto;
  height: 100%;
}

.active {
  max-height: auto;
  transition: 0.5s;
  overflow: visible;
}
.closed {
  max-height: 0;
  transition: 0.5s;
  overflow: hidden;
}

.code-snippet {
  margin-bottom: 2.5rem;
  font-size: 0.875rem;
}

.code-warning {
  opacity: 0.7;
}

pre {
  border: 1px solid #e9e8e8;
  border-radius: 3px;
  word-wrap: break-word;
  padding: 1.25rem;
  margin: 1rem 0;
  overflow: auto;
}
