.page-footer {
  margin-top: auto;
  padding: 2.5rem 0;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  border-top: 1px solid #dcdfe6;

  &__container {
    display: flex;
    flex-wrap: wrap;
  }

  &__title {
    color: $default-color;
  }
  &__logo-and-copyright {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-left: auto;
    margin-bottom: 1.25rem;
  }

  &__list {
    @include listReset();
    flex: 0 0 100%;

    @include for-size(tablet-portrait-up) {
      flex: 0 0 50%;
    }
    @include for-size(tablet-landscape-up) {
      flex: 0 0 30%;
    }
  }
  &__list-link {
    line-height: 1.6;
    color: $default-color;
    text-decoration: none;

    &:focus,
    &:hover {
      color: $link-footer-color-hover;
    }
  }
  &__list-item {
    display: flex;
    flex-direction: column;
  }
}
